<style>
  
</style>

<template>
  <div>
    <div class="search-form-wraper">
      <el-form :model="searchContent" label-position="top">
        <el-form-item label="门店类型">
          <el-select class="search-input" v-model="searchContent.storeType" filterable placeholder="门店类型" clearable size="small" @change="doSearchHandel(true)">
            <el-option
              v-for="item in storeTypeOptions"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="分店名称">
          <el-select class="search-input" v-model="searchContent.storeNo" filterable placeholder="请选择分店名称" clearable size="small" @change="doSearchHandel(true)">
            <el-option
              v-for="item in storeOptions"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="交易开始时间">
          <el-date-picker
            class="search-input" 
            v-model="searchContent.tradeStartDate"
            type="datetime"
            placeholder="交易开始时间"
            format="yyyy-MM-dd HH:mm:ss"
            value-format="yyyy-MM-dd HH:mm:ss"
            size="small"
            @change="doSearchHandel(true)">
          </el-date-picker>
        </el-form-item>
        <el-form-item label="交易结束时间">
          <el-date-picker
            class="search-input" 
            v-model="searchContent.tradeEndDate"
            type="datetime"
            placeholder="交易结束时间"
            format="yyyy-MM-dd HH:mm:ss"
            value-format="yyyy-MM-dd HH:mm:ss"
            size="small"
            @change="doSearchHandel(true)">
          </el-date-picker>
        </el-form-item>
        <el-form-item label="客户手机号">
          <el-input class="search-input" v-model.trim="searchContent.phone" placeholder="客户手机号" size="small" @keyup.enter.native="doSearchHandel(true)" clearable></el-input>
        </el-form-item>
        <el-form-item label="流水号">
          <el-input class="search-input" v-model.trim="searchContent.payOrderId" placeholder="流水号" size="small" @keyup.enter.native="doSearchHandel(true)" clearable></el-input>
        </el-form-item>
        <el-form-item label="交易来源">
          <el-select class="search-input" v-model="searchContent.tradeSource" filterable placeholder="交易类型" clearable size="small" @change="doSearchHandel(true)">
            <el-option
              v-for="item in tradeSourceOptions"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="交易类型">
          <el-select class="search-input" v-model="searchContent.tradeOrderType" filterable placeholder="交易类型" clearable size="small" @change="doSearchHandel(true)">
            <el-option
              v-for="item in tradeOrderTypeOptions"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="支付渠道">
          <el-select class="search-input" v-model="searchContent.payChannel" filterable placeholder="支付渠道" clearable size="small" @change="doSearchHandel(true)">
            <el-option
              v-for="item in payChannelOptions"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="小票号">
          <el-input class="search-input" v-model.trim="searchContent.serNo" placeholder="小票号" size="small" @keyup.enter.native="doSearchHandel(true)" clearable></el-input>
        </el-form-item>
        <el-form-item label="交易状态">
          <el-select class="search-input" v-model="searchContent.tradeStatus" filterable placeholder="交易状态" clearable size="small" @change="doSearchHandel(true)">
            <el-option
              v-for="item in tradeStatusOptions"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="doSearchHandel(true)" size="mini">搜索</el-button>
          <el-button @click="exportData" size="mini" :loading="exportLoading">导出</el-button>
        </el-form-item>
      </el-form>
    </div>

    <div class="table-wraper">
      <el-table
        v-loading="loading"
        class="fixed-height"
        :data="tableData"
        empty-text="没有查询到您想要的数据"
        style="width:100%;"
        size="small">
        <el-table-column
          label="序号"
          type="index"
          width="50">
        </el-table-column>
        <el-table-column min-width="80px" prop="cityName" label="城市" :formatter="emptyFormater"></el-table-column>
        <el-table-column min-width="80px" prop="storeNo" label="门店编码" :formatter="emptyFormater"></el-table-column>
        <el-table-column min-width="80px" prop="storeType" label="门店类型" :formatter="storeTypeFormater"></el-table-column>
        <el-table-column min-width="100px" prop="storeName" label="门店名称" :formatter="emptyFormater"></el-table-column>
        <el-table-column min-width="160px" prop="tradeDate" label="交易时间" :formatter="emptyFormater"></el-table-column>
        <el-table-column min-width="120px" prop="phone" label="会员手机号" :formatter="emptyFormater"></el-table-column>
        <el-table-column min-width="80px" prop="tradeOrderType" label="交易类型" :formatter="tradeOrderTypeFormater"></el-table-column>
        <el-table-column min-width="80px" prop="tradeSource" label="交易来源" :formatter="tradeSourceFormater"></el-table-column>
        <el-table-column min-width="80px" prop="payAmount" label="交易金额"></el-table-column>
        <el-table-column min-width="80px" prop="tradeStatusStr" label="交易状态"></el-table-column>
        <el-table-column min-width="80px" prop="refundAmount" label="退款金额"></el-table-column>
        <el-table-column min-width="100px" prop="leftStoreAmount" label="账号余额"></el-table-column>
        <el-table-column min-width="100px" prop="payChannelStr" label="支付渠道"></el-table-column>
        <el-table-column min-width="160px" prop="payOrderId" label="交易流水号" :formatter="emptyFormater"></el-table-column>
        <el-table-column min-width="180px" prop="payThirdOrderId" label="三方流水号" :formatter="emptyFormater"></el-table-column>
        <el-table-column min-width="90px" prop="serNo" label="小票号" :formatter="emptyFormater"></el-table-column>
      </el-table>
      <!-- 分页条 -->
      <el-pagination
        v-show="tableData.length>0"
        class="tc mt20"
        background
        @size-change="changeSize"
        @current-change="changeCurrent"
        :current-page.sync="pageNum"
        :page-sizes="[20, 50, 100, 150, 200]"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total">
      </el-pagination>
    </div>
  </div>
</template>

<script>
  import exportSheet from '@/utils/exportSheet'
  import * as utils from '@/utils/utils'
  const reqUrl = {
    // 获取门店列表接口
    search: '/plateform/a/store/condition/search',
    mSearch: '/plateform/a/mall/reportForm/payOrder/search',
    export: '/plateform/a/mall/reportForm/payOrder/export'
  }
  export default {
    data() {
      return {
        exportLoading: false,
        loading: false,
        pageNum: 1,
        pageSize: 20,
        total: 0,
        searchContent: {
          storeType: '',
          storeNo: null,
          tradeStartDate: `${utils.timestampToTimeNoHMS(new Date().getTime())} 00:00:00`,
          tradeEndDate: `${utils.timestampToTimeNoHMS(new Date().getTime())} 23:59:59`,
          phone: null,
          payOrderId: null, // 交易流水号
          payChannel: '',
          tradeOrderType: '',
          serNo: null, 
          tradeSource: '',
          tradeStatus: ''
        },
        storeTypeOptions: [
          {
            label: '全部',
            value: ''
          },
          {
            label: '直营',
            value: 1
          },
          {
            label: '加盟',
            value: 2
          }
        ],
        // 交易类型
        tradeOrderTypeOptions: [
          {
            label: '全部',
            value: ''
          },
          {
            label: '消费',
            value: 1
          },
          {
            label: '充值',
            value: 2
          }
        ],
        // 支付渠道
        payChannelOptions: [
          {
            label: '全部',
            value: ''
          },
          {
            label: '微信支付',
            value: 'WECHAT'
          },
          {
            label: '银行支付',
            value: 'BANK'
          },
          {
            label: '支付宝支付',
            value: 'ALIPAY'
          },
          {
            label: '现金支付',
            value: 'CASH'
          },
          {
            label: '线下老版本余额支付',
            value: 'DEPOSIT'
          },
          {
            label: '本来支付',
            value: 'BLAPI'
          },
          {
            label: '线下积分支付',
            value: 'POINT'
          },
          {
            label: '线上积分支付',
            value: 'OL_POINT'
          },
          {
            label: '线上会员余额支付',
            value: 'OL_DEPOSIT'
          },
          {
            label: '支付宝代支付',
            value: 'ALIPAY_AGENT'
          },
          {
            label: '微信代支付',
            value: 'WECHAT_AGENT'
          },
          {
            label: '其他支付',
            value: 'OTHER'
          }
        ],
        // 交易来源
        tradeSourceOptions: [
          {
            label: '全部',
            value: ''
          },
          {
            label: '线下',
            value: 1
          },
          {
            label: '线上',
            value: 2
          }
        ],
        tradeStatusOptions: [
          {
            label: '全部',
            value: ''
          },
          {
            label: '待支付',
            value: 0
          },
          {
            label: '支付中',
            value: 1
          },
          {
            label: '支付成功',
            value: 2
          },
          {
            label: '支付失败',
            value: 3
          },
          {
            label: '支付取消',
            value: 4
          },
          {
            label: '支付关闭',
            value: 5
          },
          {
            label: '退款中',
            value: 6
          },
          {
            label: '部分退款中',
            value: 7
          },
          {
            label: '退款成功',
            value: 8
          },
          {
            label: '部分退款成功',
            value: 9
          },
          {
            label: '退款失败',
            value: 10
          },
          {
            label: '未知状态',
            value: 99
          }
        ],
        // 门店列表下拉框
        storeOptions: [],
        tableData: [],
        exportConfig: {
          cityName: '城市',
          storeNo: '门店编码',
          storeTypeStr: '门店类型',
          storeName: '门店名称',
          tradeDate: '交易时间',
          phone: '会员手机号',
          tradeOrderTypeStr: '交易类型',
          tradeSourceStr: '交易来源',
          payAmount: '交易金额',
          tradeStatusStr: '交易状态',
          refundAmount: '退款金额',
          leftStoreAmount: '账号余额',
          payChannelStr: '支付渠道',
          payOrderId: '交易流水号',
          payThirdOrderId: '三方流水号',
          serNo: '小票号'
        }
      }
    },
    created() {
      // 获取门店
      this.getStoreList()
    },
    mounted() {
      this.doSearchHandel(true)
    },
    methods: {
      emptyFormater(row, column, cellValue) {
        return cellValue || '-'
      },
      storeTypeFormater(row, column, cellValue) {
        if(cellValue === 1) {
          return '直营'
        }else if(cellValue === 2) {
          return '加盟'
        }else {
          return '-'
        }
      },
      tradeOrderTypeFormater(row, column, cellValue) {
        if(cellValue === 1) {
          return '消费'
        }else if(cellValue === 2) {
          return '充值'
        }else {
          return '-'
        }
      },
      tradeSourceFormater(row, column, cellValue) {
        if(cellValue === 1) {
          return '线下'
        }else if(cellValue === 2) {
          return '线上'
        }else {
          return '-'
        }
      },
      /**
       * [getStoreList 获取门店列表]
       * @return {[type]} [description]
       */
      getStoreList() {
        this.$http.post(reqUrl.search).then(res => {
          if(res.code === 0) {
            if(res.data && res.data.length) {
              let optionsArr = []
              for(let item of res.data) {
                let optionItem = {
                  value: item.storeNo,
                  label: item.storeNo +'-'+ item.storeName
                }
                optionsArr.push(optionItem)
              }
              optionsArr.unshift({value: null, label: '全部'})
              this.storeOptions = optionsArr
            }
            return
          }
          throw new Error(res.msg)
        }).catch(e => {
          console.log("提示(获取门店列表):", e.message)
        })
      },
      /**
       * [changeSize description]
       * @param    {[type]}                 value [description]
       * @return   {[type]}                       [description]
       */
      changeSize (value) {
        this.pageSize = value
        this.doSearchHandel()
      },
      /**
       * [changeCurrent 点击分页条]
       * @param    {[type]}                 value [当前页]
       * @return   {[type]}                       [description]
       */
      changeCurrent (value) {
        this.pageNum = value
        this.doSearchHandel()
      },
      doSearchHandel(_param) {
        this.loading = true
        if(_param === true) {
          this.pageNum = 1
        }
        this.$http.post(reqUrl.mSearch, {...this.searchContent, pageNum: this.pageNum, pageSize: this.pageSize }).then(res => {
          this.loading = false
          if(res.code === 0) {
            if(res.data && res.data.result && res.data.result.length) {
              let resultTemp = res.data.result.map((item, index) => {
                let cPayChannel = this.payChannelOptions.filter((j) => {
                  return j.value === item.payChannel
                })[0]
                item.payChannelStr = cPayChannel && cPayChannel.label ? cPayChannel.label : ''

                let cTradeStatus = this.tradeStatusOptions.filter((j) => {
                  return j.value === item.tradeStatus
                })[0]
                item.tradeStatusStr = cTradeStatus && cTradeStatus.label ? cTradeStatus.label : '-'
                return item
              })
              this.tableData = resultTemp
            }else {
              this.tableData = []
            }
            this.total = res.data.total
            return 
          }
          throw new Error(res.msg)
        }).catch(e => {
          this.loading = false
          this.$notify.error({
            title: '提示(会员基础数据):',
            message: e.message
          })
        })
      },
      exportData() {
        this.exportLoading = true
        this.$http.post(reqUrl.export, {...this.searchContent}).then(res => {
          this.exportLoading = false
          if(res.code === 0) {
            if(res.data && res.data.result && res.data.result.length) {
              let resultTemp = res.data.result.map((item, index) => {
                let cStoreType = this.storeTypeOptions.filter((j) => {
                  return j.value === item.storeType
                })[0]
                item.storeTypeStr = cStoreType && cStoreType.label ? cStoreType.label : ''

                let cTradeOrderType = this.tradeOrderTypeOptions.filter((j) => {
                  return j.value === item.tradeOrderType
                })[0]
                item.tradeOrderTypeStr = cTradeOrderType && cTradeOrderType.label ? cTradeOrderType.label : ''

                let cTradeSource = this.tradeSourceOptions.filter((j) => {
                  return j.value === item.tradeSource
                })[0]
                item.tradeSourceStr = cTradeSource && cTradeSource.label ? cTradeSource.label : ''

                let cPayChannel = this.payChannelOptions.filter((j) => {
                  return j.value === item.payChannel
                })[0]
                item.payChannelStr = cPayChannel && cPayChannel.label ? cPayChannel.label : ''

                let cTradeStatus = this.tradeStatusOptions.filter((j) => {
                  return j.value === item.tradeStatus
                })[0]
                item.tradeStatusStr = cTradeStatus && cTradeStatus.label ? cTradeStatus.label : ''
                return item
              })
              let tableDataArr = resultTemp
              exportSheet.exportFileByConfig(tableDataArr, '财务明细报表.xlsx', this.exportConfig)
              setTimeout(() => {
                this.$message.success("导出成功")
              }, 500)
            }else {
              this.$notify.warning({
                title: '温馨提示',
                message: '没有查询到要导出的数据'
              });
            }
            return 
          }
          throw new Error(res.msg)
        }).catch(e => {
          this.exportLoading = false
          this.$notify.error({
            title: '提示(导出数据)：',
            message: e.message
          })
        })
      }
    }
    
  }
</script>


